import React, {useCallback, useEffect, useState} from "react";
import {render} from "react-dom";
import {Button, FormControl, FormControlLabel, FormLabel,TextField as Input, Radio, RadioGroup} from "@mui/material";


let App = (): React.ReactElement => {
    const [part, SetPart] = useState("1");
    const [w,SetW] = useState("640");
    const [h,SetH] = useState("480");
    let onPlay = useCallback(() => {

        fetch(`rom/${part}.nds`).then(async (result) => {
            let blob = await result.blob();
            let iframe = eval("window.iframe") as HTMLIFrameElement;
            let url = URL.createObjectURL(blob);

            let src=`
                <script>
                 EJS_player = '#game';
                 EJS_gameUrl = '${url}'; // Url to Game rom
                 EJS_core = 'nds';
                 EJS_gameName="${part}";
                 EJS_pathtodata = 'data/'; //path to all of the wasm and js files. MUST all be in the same directory!!
                </script>
                <script src="data/loader.js"></script>
                <div style='width: ${w}px; height: ${h}px;'>
                    <div id='game'></div>
                </div>
            `;
            window.open(".")?.document.write(src);
        })
    }, [part,w,h])
    return (<>
        <FormControl>
            <FormLabel id="demo-radio-buttons-group-label">Select Game</FormLabel>
            <RadioGroup
                aria-labelledby="demo-radio-buttons-group-label"
                value={part}
                name="radio-buttons-group"
            >
                <FormControlLabel value="1" control={<Radio/>} label="Part 1" onClick={() => SetPart("1")}/>
                <FormControlLabel value="2" control={<Radio/>} label="Part 2" onClick={() => SetPart("2")}/>
                <br/>
                <Input placeholder={"Width"} type={"text"} variant={"outlined"} value={w} onChange={(event)=>SetW(event.target.value)}/>
                <Input placeholder={"Height"} type={"text"} value={h} onChange={(event)=>SetH(event.target.value)}/>
            </RadioGroup>
            <Button variant={"outlined"} size={"medium"} onClick={onPlay}>Play</Button>
        </FormControl>


    </>)
};
render(
    <>
        <App/>
    </>,
    document.querySelector("#root")
);
